import React from "react";

// reactstrap components
import {
  Container,
  Row,
  Col,
  Carousel,
  CarouselItem,
  CarouselIndicators
} from "reactstrap";

// core components

const items = [
  {
    src: require("assets/img/photo5.jpg"),
    altText: "",
    caption: ""
  },
  {
    src: require("assets/img/photo1.jpg"),
    altText: "策劃會",
    caption: "策劃會"
  },
  {
    src: require("assets/img/photo2.jpg"),
    altText: "仁濟醫院 午間閒情",
    caption: "仁濟醫院 午間閒情"
  },
  // {
  //   src: require("assets/img/photo3.jpg"),
  //   altText: "浸禮",
  //   caption: "浸禮"
  // },
  {
    src: require("assets/img/photo4.jpg"),
    altText: "聖誕街頭佈道",
    caption: "聖誕街頭佈道"
  }
];

function CarouselSection() {
  const [activeIndex, setActiveIndex] = React.useState(0);
  const [animating, setAnimating] = React.useState(false);
  const onExiting = () => {
    setAnimating(true);
  };
  const onExited = () => {
    setAnimating(false);
  };
  const next = () => {
    if (animating) return;
    const nextIndex = activeIndex === items.length - 1 ? 0 : activeIndex + 1;
    setActiveIndex(nextIndex);
  };
  const previous = () => {
    if (animating) return;
    const nextIndex = activeIndex === 0 ? items.length - 1 : activeIndex - 1;
    setActiveIndex(nextIndex);
  };
  const goToIndex = newIndex => {
    if (animating) return;
    setActiveIndex(newIndex);
  };
  return (
    <>
      <div className="section" id="carousel" style={{marginBottom: 100}}>
        <div>
          <Container>
            <h3 className="title">最新消息</h3>
          </Container>
          <Row className="justify-content-center" style={{marginRight : 0, marginLeft: 0}}>
            <Col lg="8" md="12">
              <Carousel
                activeIndex={activeIndex}
                next={next}
                previous={previous}
              >
                <CarouselIndicators
                  items={items}
                  activeIndex={activeIndex}
                  onClickHandler={goToIndex}
                />
                {items.map(item => {
                  return (
                    <CarouselItem
                      onExiting={onExiting}
                      onExited={onExited}
                      key={item.src}
                    >
                      <img src={item.src} alt={item.altText} />
                      <div className="carousel-caption d-none d-md-block">
                        <h5>{item.caption}</h5>
                      </div>
                    </CarouselItem>
                  );
                })}
                <a
                  className="carousel-control-prev"
                  data-slide="prev"
                  href="#pablo"
                  onClick={e => {
                    e.preventDefault();
                    previous();
                  }}
                  role="button"
                >
                  <i className="fas fa-chevron-left"></i>
                </a>
                <a
                  className="carousel-control-next"
                  data-slide="next"
                  href="#pablo"
                  onClick={e => {
                    e.preventDefault();
                    next();
                  }}
                  role="button"
                >
                  <i className="fas fa-chevron-right"></i>
                </a>
              </Carousel>
            </Col>
          </Row>
        </div>
      </div>
      <hr style={{width: '80%'}} />
    </>
  );
}

export default CarouselSection;
